import {
  GET_PROFILE_EDITED_REQUEST,
  GET_PROFILE_EDITED_FAILURE,
  GET_PROFILE_EDITED_SUCCESS
} from "../types/types";

const initialValue = {
  photo:
    "data:image/jpeg;base64,",
  firstName: "Joe",
  lastName: "Smith",
  birthday: "",
  country: "Ukraine",
  city: "Kyiv",
  status: "Love is life. And if you miss love, you miss life.",
  email: "smithulass@gmail.com",
  cell: "098157788",
  isLoading: false
};
export const onProfileEdit = (state = initialValue, action) => {
  switch (action.type) {
    case GET_PROFILE_EDITED_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_PROFILE_EDITED_SUCCESS:
      return {
        ...state,
        ...action.payload,
        isLoading: false,
      };
    case GET_PROFILE_EDITED_FAILURE:
      return {
        ...state,
        isLoading: false
      };
    default:
      return state;
  }
};
